
import { animate, keyframes, style, transition, trigger } from '@angular/animations';
import { Component, OnInit, ViewChild, Input, OnChanges, OnDestroy } from '@angular/core';
import { CdkVirtualScrollViewport } from '@angular/cdk/scrolling';
import { BehaviorSubject } from 'rxjs';

import { BaseComponent } from '../../../shared/globals/base.component';
import { AppService } from '../../../services/app.service';
import { IBooking } from '../../../services/data/bookings.service';

import * as moment from 'moment';
import { Utils } from '../../../shared/utility.class';
import { IUser } from '../../../services/data/users.service';

@Component({
    selector: 'schedule-event-list',
    templateUrl: './event-list.component.html',
    styleUrls: ['./event-list.component.scss'],
    animations: [
        trigger('show', [
            transition('void => event', animate('.3s ease-out', keyframes([
                style({ height: 0, opacity: 0, offset: 0 }),
                style({ height: '80px', opacity: 1, offset: 1 })
            ])))
        ])
    ]
})
export class ScheduleEventListComponent extends BaseComponent implements OnInit, OnChanges, OnDestroy {
    /** User to show events for */
    @Input() public user: IUser;
    @Input() public date = moment().valueOf();
    public model: any = {};
    public items: BehaviorSubject<IBooking[]> = new BehaviorSubject([]);
    public selected_index: number;

    // @ViewChild('list') private list: ElementRef;

    @ViewChild(CdkVirtualScrollViewport, { static: true }) private viewport: CdkVirtualScrollViewport;

    constructor(private service: AppService) {
        super();
    }

    public ngOnInit() {
        this.model.days = 15;
        this.checkScroll();
        this.items.subscribe((res) => this.model.list = res);
        this.subscription('bookings', this.service.Bookings.listen('timeline', (timeline) => {
            const date = moment().startOf('d');
            const end = moment(date).add(this.model.days, 'd');
            let list: IBooking[] = [];
            for (; date.isBefore(end, 'm'); date.add(1, 'd')) {
                list = [ ...list, ...(timeline[date.format('YYYY/MM/DD')] || []) ];
            }
            list = Utils.unique(list, 'order_id');
            if (this.user) {
                list = list.filter(i =>
                    i.organiser.email === this.user.email
                    || !!i.attendees.find(j => j.email === this.user.email)
                );
            }
            this.model.list = list
            this.processBookings();
        }));
        this.loadBookings();
        this.interval('update', () => this.updateBookings(), 10 * 1000);
    }

    public ngOnChanges(changes: any) {
        if (changes.date) {
            this.timeout('date_change', () => this.changeDate(this.date), 500);
        }
        if (changes.user) {
            this.updateBookings();
            this.interval('update', () => this.updateBookings(), 10 * 1000);
        }
    }

    public ngOnDestroy(): void {
        super.ngOnDestroy();
        this.clearInterval('update');
    }

    public loadBookings() {
        if (this.model.loading) { return; }
        return new Promise((resolve, reject) => {
            this.model.loading = true;
            const date = moment().startOf('d');
            const end = moment(date).add(Math.max(3, this.model.days), 'd');
            const user = this.service.Users.current();
            if (!this.user) {
                this.user = this.service.Users.current();
            }
            const user_list = user.delegates && user.delegates.length > 0 ? [user.email, ...user.delegates] : [user.email];
            this.service.Bookings.query({
                email: this.user ? this.user.email : user_list.reduce((a, v) => (a ? a + ',' : a) + v, ''),
                from: date.unix(), to: end.unix()
            }).then((items) => {
                this.service.Bookings.clear({ from: date.valueOf(), to: end.valueOf() });
                this.service.Bookings.updateList(Utils.unique(items, 'icaluid'));
                this.model.loading = false;
                resolve();
            }, _ => {
                this.model.loading = false;
                reject();
            });
        });
    }

    public checkScroll() {
        this.timeout('check_scroll', () => {
            if (!this.viewport) { return this.timeout('atBottom', () => this.checkScroll()); }
            const start = this.viewport.getRenderedRange().start;
            const end = this.viewport.getRenderedRange().end;
            const total = this.viewport.getDataLength();
            const from_start = moment().add(this.model.from_start, 'd').format('YYYY-MM-DD');
            const items = this.items.getValue();
            if (end === total) {
                this.atBottom();
            } else if (this.model.from_start > 0 && items.indexOf(items.find(i => i.id === from_start)) > start) {
                this.atTop();
            }
        }, 100);
    }

    public atBottom() {
        if (this.model.loading) { return; }
        this.model.days += 3;
        this.interval('update', () => this.updateBookings(), 5 * 1000);
        this.updateBookings();
    }

    public getViewLocation() {
        if (this.viewport) {
            const start = Math.floor(this.viewport.measureScrollOffset() / 80);
            const items = this.items.getValue();
            if (items && items.length > 0 && start < items.length) {
                const item = items[start];
                return item ? item.id : null;
            }
        }
        return null;
    }

    public atTop() {
        if (this.model.loading || this.model.from_start <= 0) { return; }
        const range = this.viewport.getRenderedRange();
        const now = moment().startOf('d');
        const items = this.items.getValue();
        const middle = Math.floor((range.start + range.end) / 2);
        const time = moment(items && items[middle] ? items[middle].date : undefined);
        this.model.from_start = Math.floor(moment.duration(time.diff(now)).asDays());
        this.updateBookings();
    }

    public scrollTo(id: string, offset: number = 0, smooth: boolean = true) {
        if (!id || (this.model.ignore && id !== this.model.ignore)) { return; }
        const items = this.items.getValue();
        const index = items.findIndex(i => i.id === id);
        if (index >= 0) {
            this.viewport.scrollToOffset(index * 80 + offset, smooth ? 'smooth' : 'auto');
        }
        this.timeout('ignore', () => this.model.ignore = null, 1000);
    }

    public changeDate(date: number) {
        const now = moment().startOf('d');
        const day = moment(date);
        let time = moment(day).subtract(3, 'd');
        if (time.isBefore(now, 'd')) { time = moment(now); }
        if (day.diff(now, 'd') < this.model.days) {
            this.model.ignore = day.format('YYYY-MM-DD');
            this.scrollTo(day.format('YYYY-MM-DD'));
        } else {
            this.model.from_start = time.diff(now, 'd');
            this.model.days = this.model.from_start + 8;
            this.updateBookings().then(() => {
                this.timeout('scroll', () => this.scrollTo(day.format('YYYY-MM-DD')), 1000);
            });
        }
    }

    public updateBookings() {
        return new Promise((resolve, reject) => {
            if (this.model.loading) { return reject(); }
            if (!this.viewport) {
                return this.timeout('update_bookings', () => this.updateBookings().then(resolve, reject));
            }
            if (!this.user) {
                this.user = this.service.Users.current();
            }
            const range = this.viewport.getRenderedRange();
            const now = moment().startOf('d');
            const items = this.items.getValue();
            const middle = Math.floor((range.start + range.end) / 2);
            const date = items && items[middle] ? items[middle].date : undefined;
            const start = moment(date).subtract(3, 'd').startOf('d');
            const end = moment(date).add(3, 'd').endOf('d');
            const from = start.isBefore(now, 'd') ? now.unix() : start.unix();
            if (moment(from).isSameOrAfter(end)) { return; }
            this.setLoading(start.valueOf(), end.valueOf());
            const user = this.service.Users.current();
            const user_list = user.delegates && user.delegates.length > 0 ? [user.email, ...user.delegates] : [user.email];
            this.model.loading = true;
            this.service.Bookings.query({
                email: this.user ? this.user.email : user_list.reduce((a, v) => (a ? a + ',' : a) + v, ''),
                from,
                to: end.unix()
            }).then((list) => {
                this.service.Bookings.clear({ from: start.isBefore(now, 'd') ? now.valueOf() : start.valueOf(), to: end.valueOf() });
                this.service.Bookings.updateList(list);
                this.model.loading = false;
                resolve();
            }, _ => { this.model.loading = false; reject()});
        });
    }

    public setLoading(start: number, end: number) {
        const from = moment(start);
        const to = moment(end);
        const items: any[] = this.items.getValue();
        for (; from.isBefore(to, 'm'); from.add(1, 'd')) {
            const item = items.find(i => i.id === from.format('YYYY-MM-DD'));
            if (item) { item.loading = true; }
        }
    }

    public processBookings() {
        this.timeout('process', () => {
            const offset = this.viewport.measureScrollOffset() % 80;
            const scroll_id = this.getViewLocation();
            this.model.list.sort((a, b) => a.date - b.date);
            let list: any[] = [];
            const now = moment();
            const date = moment().startOf('d');
            const end = moment(date).add(this.model.days, 'd').endOf('d');
            for (; date.isSameOrBefore(end, 'd'); date.add(1, 'd')) {
                list.push({
                    order_id: date.format('YYYY-MM-DD'),
                    id: date.format('YYYY-MM-DD'),
                    type: 'date',
                    today: now.isSame(date, 'd'),
                    title: date.format('dddd, DD MMM'),
                    value: date.format('YYYY-MM-DD'),
                    date: date.valueOf()
                });
                const length = list.length;
                for (const bkn of this.model.list) {
                    if (bkn.type && bkn.type !== 'event') { continue; }
                    const bkn_date = moment(bkn.for_date || bkn.date);
                    if (bkn_date.isSame(date, 'd')) {
                        const item = { ...bkn, room: bkn.room, room_list: bkn.room_list, status: bkn.status, type: 'event' };
                        list.push(item);
                    }
                }
                if (list.length === length) {
                    list.push({ order_id: `${date.format('YYYY-MM-DD')}-no-items`, type: 'no-items', title: 'No events', date: date.valueOf() });
                }
            }
            list.forEach(bkn => {
                const rooms = bkn.spaces || [];
                console.log('Rooms:', bkn.title, rooms, bkn.approval_status, bkn);
                bkn.is_declined = rooms.reduce((state, room) => state || bkn.approval_status[room] === 'declined', false);
                bkn.is_tentative = rooms.reduce((state, room) => state || (bkn.approval_status[room] || '').indexOf('tentative') >= 0, false);
            })
            list.sort((a, b) => (a.for_date || a.date) - (b.for_date || b.date));
            list = Utils.unique(list, 'order_id');
            this.items.next(list);
            this.model.bookings = list.filter(i => i.id);
            if (this.model.ignore && !!list.find(i  => i.id === this.model.ignore)) {
                this.scrollTo(this.model.ignore);
            } else {
                this.scrollTo(scroll_id, offset, false);
            }
        }, 50);
    }

    public view(item: any) {
        this.model.mainIndex = this.model.bookings.indexOf(item);

        const idx = this.model.mainIndex;
        item.disableSwitch = idx === 0 ? 'first' : (idx === this.model.bookings.length - 1 ? 'last' : 'undefined');
        if (this.service) {
            this.service.Overlay.openModal('meeting-details', { data: {
                booking: this.service.Bookings.item(item.id),
                as_delegate: this.user && this.user.email !== this.service.Users.current().email,
                delegate: this.user.email
            } }, (event) => {
                if (event.type === 'next') { // Show next booking
                    this.timeout('next_booking', () => this.nextbooking());
                } else if (event.type === 'delete') { // Delete active booking
                    this.timeout('delete_booking', () => this.deletebooking());
                } else if (event.type === 'previous') { // Show previous booking
                    this.timeout('previous_booking', () => this.previousbooking());
                } else if (event.type === 'check-in') { // Checkin to active booking
                    this.timeout('delete_booking', () => this.checkin());
                }
                event.close();
            });
        }
    }

    public checkin() {
        const index = this.model.mainIndex;
        this.model.bookings[index].checkin = true;
    }

    public deletebooking() {
        this.model.bookings.splice(this.model.mainIndex, 1);
    }

    public nextbooking() {
        const index = this.model.mainIndex;
        if (index < this.model.bookings.length - 1) {
            this.view(this.model.bookings[index + 1]);
        } else {
            this.view(this.model.bookings[index]);
        }
    }

    public previousbooking() {
        const index = this.model.mainIndex;
        if (index > 0) {
            this.view(this.model.bookings[index - 1]);
        } else {
            this.view(this.model.bookings[index]);
        }
    }

}
